/*----------------------------------------
Transforms */
.lt_dmc-container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
  max-width: 1600px;
}
.lt_dmc-container .carousel {
  pointer-events: none;
}
.lt_dmc-container .carousel-inner {
  overflow: visible;
}

.lt_dmc-carousel .carousel-item {
  opacity: 0;
  transition: opacity 0.6s ease-out;
}
.lt_dmc-carousel .carousel-item.active {
  opacity: 1;
}
@media (max-width: 991px) {
  .lt_dmc-carousel {
    margin: 0 auto;
    max-width: 25rem;
  }
}
@media (min-width: 992px) {
  .lt_dmc-carousel .carousel-inner {
    margin-left: -12.5%;
    width: 112.5%;
  }
}

.lt_dmc-text .carousel-inner {
  transition: height 0.75s ease-out;
}
.lt_dmc-text .display-2 {
  opacity: 0;
  -webkit-transform: translate(0, 2rem);
          transform: translate(0, 2rem);
  transition: opacity 1s ease-out, -webkit-transform 1.25s ease-out;
  transition: opacity 1s ease-out, transform 1.25s ease-out;
  transition: opacity 1s ease-out, transform 1.25s ease-out, -webkit-transform 1.25s ease-out;
}
@media (min-width: 768px) {
  .lt_dmc-text .display-2 {
    -webkit-transform: translate(-2rem, 0);
            transform: translate(-2rem, 0);
  }
  .lt_dmc-text .display-2:nth-child(2) {
    transition-delay: 0.3s;
  }
}
@media (min-width: 1200px) {
  .lt_dmc-text .display-2 {
    -webkit-transform: translate(-3rem, 0);
            transform: translate(-3rem, 0);
  }
}
@media only screen and (min-width: 1440px) {
  .lt_dmc-text .display-2 {
    -webkit-transform: translate(-4rem, 0);
            transform: translate(-4rem, 0);
  }
}
.lt_dmc-text .carousel-item.active .display-2 {
  opacity: 1;
  -webkit-transform: translate(0);
          transform: translate(0);
}

@media (min-width: 992px) {
  .lt_dmc-content {
    padding: 0 4%;
  }
}
@media (min-width: 1200px) {
  .lt_dmc-content {
    padding: 0 5%;
  }
}
@media only screen and (min-width: 1440px) {
  .lt_dmc-content {
    padding: 0 7.5rem;
  }
}

.lt_dmc-tabs {
  text-align: center;
  margin-left: 0;
  margin-right: 0;
}
.lt_dmc-tabs .nav-item {
  -webkit-flex-grow: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.lt_dmc-tabs .nav-link {
  color: #707070;
  margin: 0 0 -1px;
  padding: 0.75rem 0.5rem;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: bold;
  border-bottom-width: 1px;
}
.lt_dmc-tabs .nav-link.active, .lt_dmc-tabs .nav-link:hover, .lt_dmc-tabs .nav-link:focus {
  color: #0078aa;
}
@media (min-width: 992px) {
  .lt_dmc-tabs {
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
  }
  .lt_dmc-tabs .nav-item {
    -webkit-flex-grow: 0;
        -ms-flex-positive: 0;
            flex-grow: 0;
  }
  .lt_dmc-tabs .nav-link {
    padding: 1rem 1.5rem;
  }
}